<template>
  <div class="con">
    <publicTop></publicTop>
    <!-- 头部 -->
    <div
      class="head-bg"
      :style="`background-image: url(${url});background-size: 100% 100%;`"
    >
      <!-- <img src="@/assets/Mask.png" alt="" class="head-bg-img" /> -->

      <div class="head-bg-t">
        <div class="head-bg-l" @click="goBack">
          <img src="@/assets/icon-back-white.png" alt="" />
        </div>

        <div class="head-bg-text">{{ typeText }}</div>
      </div>
      <div class="head-bg-ending">
        <div class="zhao-head-div">
          <input
            type="text"
            v-model="inputContent"
            placeholder="请输入公司/职位"
          />
          <img src="@/assets/icon-search.png" alt="" @click="goSear" />
        </div>
        <div class="head-bg-ending-r" @click="goSelect">
          <div class="head-bg-ending-r-text">筛选</div>
          <img src="@/assets/icon_d.png" alt="" />
        </div>
      </div>
    </div>
    <!-- 中部 -->
    <!-- <div class="footer-top">
      <div class="footer-top-l">
        <div
          v-for="(item, index) in tabArray"
          :key="index"
          @click="goToggle(index)"
          class="footer-top-l-title"
          :class="tabIndex == index ? 'actived' : ''"
        >
          {{ item.name }}
        </div>
      </div>
      <div class="footer-top-r">
        <div class="footer-top-r-l" @click="goCity">
          <div class="footer-top-r-l-text">地区</div>
          <img src="@/assets/icon_d.png" alt="" />
        </div>
        <div class="footer-top-r-r" @click="goSelect">
          <div class="footer-top-r-l-text">筛选</div>
          <img src="@/assets/icon_d.png" alt="" />
        </div>
      </div>
    </div> -->

    <!-- 底部列表 -->
    <div class="list-all" v-if="allL.length != 0">
      <van-list
        :immediate-check="false"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        loading-text="下拉查看更多"
        @load="kh"
      >
        <div
          v-for="(item, index) in allL"
          :key="index"
          class="list-all-li"
          @click="goDe(item.id)"
        >
          <!-- <img src="@/assets/ik.png" alt="" class="list-all-li-ab" /> -->
          <div class="list-all-li-ab1">{{ item.startTime.slice(0, 10) }}</div>
          <div class="list-all-li-l">
            <img
              :src="item.corporationinfo.logoUrl"
              alt=""
              v-if="item.corporationinfo.logoUrl"
            />
            <img src="@/assets/momo.png" alt="" v-else />
          </div>
          <div class="list-all-li-r">
            <div class="list-all-li-r-t">
              {{ item.title }}
            </div>
            <div class="list-all-li-r-m">
              <div class="list-all-li-r-m-r" :style="`color:${bgc}`">
                {{ item.corporationinfo.name }}
              </div>
            </div>
            <div class="list-all-li-r-r">
              {{ item.positionNum }}个职位 | {{ item.education }}
            </div>
          </div>
        </div>
      </van-list>
    </div>

    <!-- 没有数据时 -->
    <div class="list-all-no" v-if="allL.length == 0">
      <img src="@/assets/noData.png" alt="" class="list-all-no-img" />
      <div class="list-all-no-text">暂无数据</div>
    </div>

    <HomeTop :i="checkIndex" />
  </div>
</template>

<script>
import qs from "qs";
import HomeTop from "@/components/homeTop.vue";
import publicTop from "@/components/publicTop.vue";
export default {
  name: "recruitmentinfoList",
  components: {
    HomeTop,
    publicTop,
  },
  data() {
    return {
      checkIndex: 2,
      tabIndex: 0,
      tabList: [
        { name: "招聘信息" },
        { name: "实习信息" },
        { name: "双选会" },
        { name: "宣讲会" },
      ],
      inputContent: "",
      tabArray: [{ name: "推荐" }, { name: "最新" }],
      url: require("../assets/Mask.png"),
      loading: false,
      finished: false,
      pageNo: 1,
      allL: [],
      bgc: this.GLOBAL.bgc,
      typeText: "",
      zhaTT: 8888,
      valueNature: "",
      valueIndustry: "",
    };
  },
  watch: {
    $route: {
      immediate: true, // 一旦监听到路由的变化立即执行
      handler(to, from) {
        console.log("监听路由：" + JSON.stringify(to.name), to.name, to, from);
      },
    },
    inputContent(newV, oldV) {
      console.log(newV, oldV);
      this.$store.commit("updataInputZhao", newV);
    },
  },
  beforeRouteLeave(to, from, next) {
    console.log(to, from, next);
    if (to.path === "/recruitmentinfoForm") {
      this.$store.commit("updataList", this.allL);
      this.$store.commit("updataPage", this.pageNo);
    } else if (to.path === "/zhaoMoreSelect") {
      this.$store.commit("updataList", []);
      this.$store.commit("updataPage", "");
    } else {
      // this.$store.commit("updataValue", "");
      // this.$store.commit("updataValue1", "");
      sessionStorage.setItem("Value", "");
      sessionStorage.setItem("Value1", "");
      this.$store.commit("updataIndex", 9999999993);
      this.$store.commit("updataIndex1", 99999999999993);
      this.$store.commit("updataList", []);
      this.$store.commit("updataPage", "");
      this.$store.commit("updataInputZhao", "");
    }
    next();
  },
  methods: {
    goSear() {
      console.log(this.inputContent, "search");
      this.$store.commit("updataInputZhao", this.inputContent);
      this.allL = [];
      this.kh();
    },
    goBack() {
      this.$router.go(-1);
    },
    goDe(id) {
      // this.$store.commit("updataDetailId", id);
      // this.$router.push({ name: "zhaoDetail" });

      this.$router.push({
        path: "/recruitmentinfoForm",
        query: { type: "", positionDetailId: id },
      });
    },
    goCity() {
      this.$router.push({ name: "zhaoMoreCity" });
    },
    goSelect() {
      this.$router.push({ name: "zhaoMoreSelect" });
    },
    goToggle(index) {
      this.tabIndex = index;
    },
    kh() {
      let that = this;
      that.loading = true;
      console.log(that.$store.getters.getPage, "页数");
      if (that.$store.getters.getPage) {
        that.pageNo = that.$store.getters.getPage;
      }
      if (that.pageNo == 1) {
        that.allL = [];
      }
      if (that.allL.length == 0) {
        that.pageNo = 1;
      }
      const params = {
        pageNo: that.pageNo,
        pageSize: 16,
        title: that.inputContent,
        positionType: that.zhaTT,
        corporationNature: that.valueNature,
        "corporationinfo.industry": that.valueIndustry,
      };
      console.log(params);
      that.$axios
        .post("/f/recruitmentinfo/ajax_frontRecruitinfo", qs.stringify(params))
        .then((res) => {
          console.log(res, "招聘数据list");
          if (res.data.state == 1) {
            that.loading = false;
            if (that.pageNo <= res.data.object.totalPage) {
              ++that.pageNo;
              that.$store.commit("updataPage", that.pageNo);
              if (res.data.object.list && res.data.object.list.length != 0) {
                that.allL = [...that.allL, ...res.data.object.list];
              }
              console.log(that.allL, that.allL.length, 56);
            } else {
              console.log("有吗");
              that.$store.commit("updataPage", "");
              that.finished = true;
            }
          } else {
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
  },
  created() {
    // console.log(this.$store.getters.getTypeText, "89");
    // let type = this.$store.getters.getTypeText;
    // console.log(type);
    // if (type == 0) {
    //   this.typeText = "招聘信息";
    //   this.zhaTT = 1;
    // } else {
    //   this.typeText = "实习信息";
    //   this.zhaTT = 2;
    // }
    console.log(this.$route.query.type, "89");
    let type = this.$route.query.type;
    console.log(type);
    if (type == 1) {
      this.typeText = "招聘信息";
      this.zhaTT = 1;
    } else {
      this.typeText = "实习信息";
      this.zhaTT = 2;
    }
    console.log(
      this.allL.length,
      this.$store.getters.getPage,
      typeof this.$store.getters.getPage,
      "all9",
      // this.$store.getters.getValue,
      // this.$store.getters.getValue1,
      this.$store.getters.getIndex,
      this.$store.getters.getIndex1,
      sessionStorage.getItem("Value"),
      sessionStorage.getItem("Value1")
    );

    console.log(this.$store.getters.getInputZhao, "allzhao");
    if (this.$store.getters.getInputZhao) {
      this.inputContent = this.$store.getters.getInputZhao;
      console.log(this.inputContent);
    } else {
      this.inputContent = "";
    }

    if (
      sessionStorage.getItem("Value") == "null" ||
      sessionStorage.getItem("Value") == "" ||
      sessionStorage.getItem("Value") == null
    ) {
      this.valueNature = "";
    } else {
      this.valueNature = sessionStorage.getItem("Value");
    }

    if (
      sessionStorage.getItem("Value1") == "null" ||
      sessionStorage.getItem("Value1") == "" ||
      sessionStorage.getItem("Value1") == null
    ) {
      this.valueIndustry = "";
    } else {
      this.valueIndustry = sessionStorage.getItem("Value1");
    }

    if (this.$store.getters.getList.length != 0) {
      this.allL = this.$store.getters.getList;
      console.log(this.allL, "all");
    } else {
      this.kh();
    }
  },
  beforeDestory() {},
  destroyed() {},
  mounted: function () {},
};
</script>
<style lang="css" scoped>
.con {
  background-color: #f5f5f5;
  padding-bottom: 1.66rem;
  min-height: 100%;
}
.head-bg {
  width: 7.5rem;
  height: 2.4rem;
  padding: 0.4rem 0.32rem 0 0.32rem;
  box-sizing: border-box;
  position: relative;
  /* background-image: url("../assets/Mask.png");
  background-size: 100% 100%; */
}
.head-bg-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.head-bg-t {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 0.48rem;
  margin-bottom: 0.2rem;
}
.head-bg-ending {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.head-bg-l {
  position: absolute;
  top: 0rem;
  left: 0;
  width: 2rem;
  height: 100%;
  display: flex;
  align-items: center;
}
.head-bg-l img {
  width: 0.2rem;
  height: 0.32rem;
}
.head-bg-text {
  font-family: PingFangSC-Regular;
  font-size: 0.34rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #ffffff;
}
.zhao-head-div {
  width: 5.6rem;
  height: 0.72rem;
  background-color: #ffffff;
  box-shadow: 0rem 2rem 8rem 0rem rgba(71, 96, 215, 0.5);
  border-radius: 0.48rem;
  border: solid 0.02rem rgba(71, 96, 215, 0.6);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.4rem;
  box-sizing: border-box;
}
.zhao-head-div input {
  width: 4.2rem;
  height: 0.6rem;
  line-height: 0.6rem;
  font-family: PingFangSC-Regular;
  font-size: 0.26rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #3f4967;
  outline: none;
  border: none;
}
.zhao-head-div input::-webkit-input-placeholder {
  font-family: PingFangSC-Regular;
  font-size: 0.26rem;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.38rem;
  letter-spacing: 0rem;
  color: rgba(122, 124, 139, 0.3);
}

.zhao-head-div img {
  width: 0.24rem;
  height: 0.24rem;
}
.head-bg-ending-r {
  width: 0.98rem;
  height: 0.72rem;
  background-color: #ffffff;
  border-radius: 0.36rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.head-bg-ending-r-text {
  font-family: PingFangSC-Medium;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: -0.01rem;
  color: #6f768d;
}
.head-bg-ending-r img {
  width: 0.14rem;
  height: 0.08rem;
  margin-left: 0.1rem;
}
.footer-top {
  width: 7.5rem;
  height: 1rem;
  padding: 0 0.32rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-top-l {
  width: 3rem;
  height: 1rem;
  display: flex;
  align-items: center;
}

.footer-top-r {
  height: 1rem;
  display: flex;
  align-items: center;
}

.footer-top-r-l {
  height: 0.5rem;
  background-color: #f0f0f0;
  border-radius: 0.04rem;
  margin-right: 0.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 0.1rem;
  box-sizing: border-box;
}

.footer-top-r-l img {
  width: 0.14rem;
  height: 0.08rem;
}

.footer-top-r-l-text {
  font-family: PingFangSC-Medium;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: -0.01rem;
  color: #7a7c8b;
  margin-right: 0.05rem;
}

.footer-top-r-r {
  width: 0.94rem;
  height: 0.5rem;
  background-color: #f0f0f0;
  border-radius: 0.04rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-top-r-r img {
  width: 0.14rem;
  height: 0.08rem;
}
.footer-top-l-title {
  font-family: SourceHanSansCN-Regular;
  font-size: 0.28rem;
  font-weight: normal;
  letter-spacing: -0.01rem;
  color: #7a7c8b;
  margin-right: 0.5rem;
}
.actived {
  font-family: SourceHanSansCN-Medium;
  font-size: 0.3rem !important;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: -0.01rem;
  color: #2b2e34 !important;
}
.list-all-no {
  width: 7.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 0.32rem;
  padding-top: 0.3rem;
  box-sizing: border-box;
}
.list-all-no-img {
  width: 3.34rem;
  height: 1.76rem;
  margin-bottom: 0.7rem;
}
.list-all-no-text {
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #9fa4b3;
}
.list-all {
  width: 7.5rem;
  padding: 0 0.32rem;
  box-sizing: border-box;
}
.list-all-li {
  width: 6.86rem;
  background-color: #ffffff;
  border-radius: 0.12rem;
  margin-bottom: 0.2rem;
  display: flex;
  padding: 0.36rem 0.32rem;
  box-sizing: border-box;
  position: relative;
}

.list-all-li-ab {
  position: absolute;
  right: 0.32rem;
  top: 0.36rem;
  width: 0.36rem;
  height: 0.36rem;
}
.list-all-li-ab1 {
  position: absolute;
  font-family: PingFangSC-Regular;
  font-size: 0.22rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #c1c4ce;
  right: 0.32rem;
  bottom: 0.36rem;
}
.list-all-li-l {
  width: 0.8rem;
  height: 0.82rem;
  margin-right: 0.24rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.list-all-li-l img {
  width: 0.8rem;
  height: 0.82rem;
}
.list-all-li-r {
  width: 4.7rem;
}
.list-all-li-r-t {
  width: 4.7rem;
  font-family: PingFangSC-Medium;
  font-size: 0.34rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #0f1b41;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  word-break: break-all;
  text-align: left;
}
.list-all-li-r-m {
  display: flex;
  align-items: center;
  margin-top: 0.1rem;
  margin-bottom: 0.1rem;
}
.list-all-li-r-m-l {
  width: 0.2rem;
  height: 0.24rem;
  margin-right: 0.1rem;
}
.list-all-li-r-m-r {
  font-family: PingFangSC-Medium;
  font-size: 0.26rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  width: 4.7rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  word-break: break-all;
  text-align: left;
}
.list-all-li-r-r {
  font-family: PingFangSC-Regular;
  font-size: 0.24rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #9fa4b3;
  text-align: left;
}
</style>


